 @import url("https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap");

html {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Cabinet Grotesk', 'DM Sans', Helvetica, Arial, sans-serif;
}

// * {
//   transition: transform 0.6s;
// }

.main {
  line-height: 1.5;
  font-size: 24px;
  overflow-x: hidden;
  transition: background-color 0.5s ease-in-out;
}

.nav {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  color: white;
  z-index: 99;
  padding:  16px 32px;
  box-sizing: border-box;
  font-size: 16px;

  @media screen and (max-width: 600px) {
    display: none;
  }

  a {
    color: white;
    margin-left: 32px;
  }
}
